import axios from 'axios';

const actionCreator = {

    CARS: "CARS",
 
    getCars: function () {
        return dispatch => {
            const promise = axios.get(`http://challenge.agenciaego.tech/models`)
            this._dispatchPromise(promise, this.CARS, dispatch)
        }
    },

    getDetails: function(){
        return dispatch => {
            const promise = axios.get(`http://challenge.agenciaego.tech/models/1`)
            this._dispatchPromise(promise, this.DETAILS, dispatch)
        }
    },

    DETAILS: "DETAILS",
    _dispatchPromise: function (promise, type, dispatch) {
        return promise
            .then(({ data }) => {
                console.log(data)
                dispatch({ type: type, payload: data })
            })
            .catch(err => {
                if (err.response) {
                    alert(`Error! \n Status: ${err.response.status} \n ${err.response.data}`);
                } else {
                    alert(`Error! ${err}`);
                }
            })
    }
}

export default actionCreator