import React, { useEffect } from 'react';
import sText from '../../../styles/text.module.css';
import sSection from '../../../styles/section.module.css';
import sContainer from '../../../styles/container.module.css';
import CarsModel from './carsModel/CardModel';
import api from '../../../redux/action-creator';
import { useSelector, useDispatch } from 'react-redux';
export default function Modelos(){
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(api.getCars())
        dispatch(api.getDetails())
    }, [])

    return (
        <section className={sSection.sectionContainerModels}>
            <h2 className={sText.h2TextModelos}>Descrubrí todos los modelos</h2>
            <div className={sContainer.containerSectionFilter}>
                <div className={sContainer.containerButtonsModels}>
                    <p>Filtrar por</p>
                    <button>Todos</button>
                    <button>Autos</button>
                    <button>Pickups y Comerciales</button>
                    <button>SUVs y Crossovers</button>
                </div>
                <div>
                    {/* cambiar a botones */}
                    <select>
                        <option>Nada</option>
                        <option>De menor a mayor precio</option>
                        <option>De mayor a menor precio</option>
                        <option>Más nuevos primero</option>
                        <option>Más viejos primero</option>
                    </select>
                </div>
            </div>
            <CarsModel/>
            
        </section>
    )
}