import actionCreator from './action-creator';

const { CARS, DETAILS } = actionCreator

const initialState = {
    cars: [],
    details: []
}
const reducer = (state = initialState, action ) => {
    switch (action.type) {
        case CARS:
            return {
                ...state,
                cars: action.payload
            }
        case DETAILS: 
            return {
                ...state,
                details: action.payload
            }
        default:
            return {...state}
    }
}

export default reducer